.accordian-main {
    cursor: pointer;
    display: inline-block;
    -webkit-text-decoration: none;
    text-decoration: none;
    font-size: 14px;
    background-color: rebeccapurple;
    border: none;
    border-radius: 4px;
    padding: 10px;
    color: #fff;
  }
.list-styles { 
    list-style-type: circle;
  }
.image-style1 {
    width: 3em;
    height: 3em;
  }
.btn-source {
  margin-top: 10px;
  font-size: 12px;
  color: black !important;
}
.project-details {
  cursor: pointer;
  font-weight: bold;
  font-size: 12px;
  border-style: none !important;
  background-color: transparent;
}
.title-section {
  font-weight: bold !important;
}
.icon-detail {
  margin-right: 0.5rem;
}
.text-left {
  font-weight: normal;
}

.buttons-source {
  display: flex !important;
  justify-content: space-between !important;
  align-items: center !important;
}
